import request from "./request"

// 登录
export function LoginFn(data) {
    return request({
        method: "post",
        url: "user/login",
        data
    })
}

// 获取用户详情
export function getInfo(token) {
    return request({
        method: "get",
        url: "user/info",
        params: { token }
    })
}

// 退出
export function LoginOutFn() {
    return request({
        method: "post",
        url: "user/logout"
    })
}

// 贷款申请
export function LoanInput(data) {
    return request({
        method: "post",
        url: "loan/create",
        data
    })
}

// 申请管理
export function LoanList(data) {
    return request({
        method: "get",
        url: "loan/list",
        params: data
    })
}

// 申请管理-编辑
export function Edit(data) {
    return request({
        method: "put",
        url: "loan/update",
        data
    })
}

// 申请管理-删除
export function DeleteItem(data) {
    return request({
        method: "delete",
        url: "loan/delete/" + data.id
    })
}

// 申请管理-提交审核
export function submitToApprove(data) {
    return request({
        method: "post",
        url: "loan/submitToApprove",
        data
    })
}

// 贷款审批-初审
export function ApproveFirstList(data) {
    return request({
        method: "get",
        url: "approve/first/list",
        params: data
    })
}

// 贷款审批-查看详情
export function AppQueryInfo(data) {
    return request({
        method: "get",
        url: "loan/query",
        params: data
    })
}

// 贷款审批-初审通过
export function ApproveFirstPass(data) {
    return request({
        method: "post",
        url: "approve/first/pass",
        data
    })
}

// 贷款审批-初审拒绝
export function ApproveFirstReject(data) {
    return request({
        method: "post",
        url: "approve/first/reject",
        data
    })
}

// 标的管理
export function ContractList(data) {
    return request({
        method: "get",
        url: "contract/list",
        params: data
    })
}

// 创建管理员
export function CreateUser(data) {
    return request({
        method: 'post',
        url: 'permission/createUser',
        data
    })
}

// 列表展示
export function Userlist() {
    return request({
        method: 'get',
        url: 'user/list',
    })
}